<template>
    <div class="login_layer">
        <header class="clearfix personal-top-bar">
            <a onclick="window.history.go(-1)" class="login-back-btn"></a>
            <h2 class="personal-center">个人中心</h2>
        </header>
        <div class="personal-line">

        </div>
        <main class="main-personal-info">
            <section class="personal-info-item1 clearfix">
                <div class="p-item-box" @click="goPersonalPage($event)">
                    <img :src="uPhoto" alt="" class="user-icons">
                    <div class="user-name-sing" v-show="!isShowLogin">
                        <p class="user-namee">{{uName}}</p>
                        <p class="user-sign">{{uSign}}</p>
                    </div>
                    <div class="user-name-head" v-show="isShowLogin">
                        <p class="user-sign-login">点击登录</p>
                    </div>
                </div>
                <div class="p-item-arrow">
                    <i class="us-arrow-right"></i>
                </div>
            </section>

        </main>
        <section class="personal-info-item2">
            <ul>
                <li class="item2">
                    <a @click="goShouChangPage($event)" class="item2-link">
                        <div class="l-item2">
                            <img src="../../../assets/images/myshouc@3x.png" alt="" class="my-s-icon" data-rjs="3">
                        </div>
                        <div class="pcen-r-ietm">
                            <h3 class="my-about">我的收藏</h3>
                            <i class="my-arrow-rightbox">

                            </i>
                        </div>
                    </a>
                </li>
                <li class="item2">
                    <a @click="goJiFenPage($event)" class="item2-link">
                        <div class="l-item2">
                            <img src="../../../assets/images/my-jifen@3x.png" class="my-s-icon" data-rjs="3">
                        </div>
                        <div class="pcen-r-ietm">
                            <h3 class="my-about">我的积分</h3>
                            <i class="my-arrow-rightbox">

                            </i>
                        </div>
                    </a>
                </li>
                <li class="item2">
                    <a href="#" class="item2-link" @click="goViewPage($event)">
                        <div class="l-item2">
                            <img src="../../../assets/images/my-look@3x.png" alt="" class="my-s-icon">
                        </div>
                        <div class="pcen-r-ietm">
                            <h3 class="my-about">最近浏览</h3>
                            <i class="my-arrow-rightbox">

                            </i>
                        </div>
                    </a>
                </li>
                <li class="item2">
                    <a @click="goCommentPage($event)" class="item2-link">
                        <div class="l-item2">
                            <img src="../../../assets/images/my-pinglun@3x.png" alt="" class="my-s-icon">
                        </div>
                        <div class="pcen-r-ietm">
                            <h3 class="my-about">评论</h3>
                            <i class="my-arrow-rightbox">

                            </i>
                        </div>
                    </a>
                </li>
                <li class="item2">
                    <a @click="goZanPage($event)" class="item2-link">
                        <div class="l-item2">
                            <img src="../../../assets/images/my-zan@3x.png" alt="" class="my-s-icon">
                        </div>
                        <div class="pcen-r-ietm">
                            <h3 class="my-about">赞</h3>
                            <i class="my-arrow-rightbox">

                            </i>
                        </div>
                    </a>
                </li>
                <li class="item2">
                    <a @click="goMineTieZiPage($event)" class="item2-link">
                        <div class="l-item2">
                            <img src="../../../assets/images/my-tiezi@3x.png" alt="" class="my-s-icon">
                        </div>
                        <div class="pcen-r-ietm">
                            <h3 class="my-about">我的帖子</h3>
                            <i class="my-arrow-rightbox">

                            </i>
                        </div>
                    </a>
                </li>
                <li class="item2">
                    <a @click="goMineDownLoadedPage($event)" class="item2-link">
                        <div class="l-item2">
                            <img src="../../../assets/images/my-downloaded@3x.png" alt="" class="my-s-icon">
                        </div>
                        <div class="pcen-r-ietm">
                            <h3 class="my-about">已下载</h3>
                            <i class="my-arrow-rightbox">

                            </i>
                        </div>
                    </a>
                </li>
            </ul>
        </section>

        <section class="personal-info-item2">
            <ul>
                <li class="item2">
                    <a href="http://q.url.cn/s/1J98wXm" class="item2-link">
                        <div class="l-item2">
                            <img src="../../../assets/images/my-concat@3x.png" alt="" class="my-s-icon">
                        </div>
                        <div class="pcen-r-ietm">
                            <h3 class="my-about">联系客服</h3>
                            <i class="my-arrow-rightbox">
                            </i>
                        </div>
                    </a>
                </li>
                <li class="item2">
                    <a @click="goSuggestFeedPage($event)" class="item2-link">
                        <div class="l-item2">
                            <img src="../../../assets/images/my-fallback@3x.png" alt="" class="my-s-icon">
                        </div>
                        <div class="pcen-r-ietm">
                            <h3 class="my-about">意见反馈</h3>
                            <i class="my-arrow-rightbox">

                            </i>
                        </div>
                    </a>
                </li>
            </ul>
        </section>

    </div>
</template>
<script>
    import {mapGetters,mapActions,mapState} from 'vuex';
    export default{
        data(){
          return{
              uName:'',
              uPhoto:'',
              uSign:''
          }
        },
        computed:mapGetters(['isShowLogin']),
        mounted(){
           this.showH();
           let name = localStorage.getItem('uName');
           let photo = localStorage.getItem('uPhoto');
           let uSign = localStorage.getItem('uSign');
           if(uSign === null || uSign === ''){
               this.uSign = uSign;
           }else{
               this.uSign = "暂无签名信息"
           }
          if(name === null || name === ''){
             this.uName = "";
           }else{
             this.uName = localStorage.getItem('uName');
           }
          if(photo === null || photo === ''){
            this.uPhoto = "http://img1.cache.netease.com/catchpic/8/8A/8A9465185DE157036434805333A78E98.png";
          }else{
            this.uPhoto = localStorage.getItem('uPhoto');
          }
        },
        methods:{
            showH(){
                this.isShowLogin = localStorage.getItem('login');
                if(this.isShowLogin === false){
                  localStorage.setItem('login',false);
                  this.$store.dispatch('showLogin',false);
                }else{
                  localStorage.setItem('login',true);
                  this.$store.dispatch('showLogin',true);
                }
            },

            goShouChangPage($event){
              this.$router.push({path: '/pcenshouchang', replace: true});
                $event.preventDefault();

            },
            goJiFenPage($event){
                this.$router.push({path: '/pcenjifen', replace: true});
                $event.preventDefault();

            },
            goCommentPage($event){
                this.$router.push({path: '/personalcomment', replace: true});
                $event.preventDefault();
            },
            goZanPage($event){
                this.$router.push({path: '/personalzan',replace: true});
                $event.preventDefault();

            },
            goMineTieZiPage($event){
                this.$router.push({path: '/personaltiezi',replace: true});
                $event.preventDefault();
            },
            goMineDownLoadedPage($event){
                this.$router.push({path: '/personald',replace: true});
                $event.preventDefault();
            },
          /*  goRegisterPage($event){
                this.$router.push({path: '/register', replace: true});
                $event.preventDefault();

            },*/
            goPersonalPage($event){
               this.$router.push({path: '/personaldata',replace: true});
                $event.preventDefault();
            },
            /**
             * 跳转到意见反馈的页面
             * */
            goSuggestFeedPage($event){
                this.$router.push({path: '/suggestfeed',replace: true});
                $event.preventDefault();
            },
            goViewPage($event){
              this.$router.push({path: '/pviews',replace: true});
              $event.preventDefault();
            },
           /* settingPhoto($event){
//                console.log("去设置图片的地方");
                $event.stopPropagation();
            },*/

        }
    }
</script>
<style scoped>
    @import "../../../assets/css/personalcenter.css";
    body {
        background-color: #EDEEF0;
    }
</style>



